import { useState, FormEvent } from 'react';
import { Link } from 'react-router-dom';

import {
    PasswordInput
    , EmailInput
    , Button
} from '@ya.praktikum/react-developer-burger-ui-components';

// shared
import { useProvideAuth } from '../../services/auth';
import { TLoginFormData } from '../../shared/types/auth-types';

// styles
import styles from '../pages.module.css';

export const LoginPage = () => {

    const { signIn, request, failed, message } = useProvideAuth();

    const [formData, setFormData] = useState<TLoginFormData>({
        password: ''
        , email: ''
    });

    function loginClick(e: FormEvent<HTMLFormElement>): boolean {
        e.preventDefault();
        signIn(formData);
        return false;
    }

    const errorMessage = (
        <span className={`text text_type_main-default ${styles.errorText}`}>
             : {message}
        </span>
    );

    return (
        <div className={styles.wrapper}>
            {request && <div className='text text_type_main-medium'>  . ...</div>}
            {!request &&
                <>
                    <span className="text text_type_main-medium">
                        
                    </span>

                    <form className={styles.formColumn} onSubmit={loginClick}>
                        <EmailInput required
                            data-testid='emailLoginInput'
                            extraClass="mb-4"
                            value={formData.email}
                            onChange={e => { setFormData({ ...formData, email: e.target.value }); }}
                        />
                        <PasswordInput required
                            data-testid='passwordLoginInput'
                            extraClass="mb-4"
                            value={formData.password}
                            onChange={e => { setFormData({ ...formData, password: e.target.value }); }}
                        />
                        <div className={styles.buttonsWrapper}>
                            <Button htmlType="submit"
                                data-testid='loginButton'
                                type="primary"
                                size="medium">
                                
                            </Button>
                        </div>
                    </form>

                    {failed && errorMessage}
                    <span className="text text_type_main-small mt-10">
                         -  ?
                        <Link
                            to={{ pathname: `/register` }}
                            className={`${styles.link} ml-4`}>
                            
                        </Link>
                    </span>
                    <span className="text text_type_main-small">
                         ?
                        <Link
                            to={{ pathname: `/forgot-password` }}
                            className={`${styles.link} ml-4`}>
                             
                        </Link>
                    </span>
                </>
            }
        </div>
    );
}














