export enum TOrderState {
    Done = "done",
    Created = "created",
    Canceled = "canceled"
};

export const translateOrderState = (state: string) =>
{
    switch (state) {
        case TOrderState.Done:
            return "";
        case TOrderState.Created:
            return "";
        case TOrderState.Canceled:
            return "";
        default:
            return '';
    }
}


export enum TOrdersKey {
    All = 'all',
    User = 'user'
};